// ** Custom Components
import Sidebar from '@components/sidebar'
// ** Reactstrap Imports
import { Form, Button, Badge } from 'reactstrap'
import axios from '@src/configs/axios/axiosConfig'
import { useEffect, useState } from 'react'

const SidebarSendInvoice = (props) => {

  const { open, toggleSidebar, invite } = props
  const [emailContent, setEmailContent] = useState('')
  const [toEmail, setToEmail] = useState([])

  const getThankyouNotes = () => {

    const email_content = `<p>Dear {invitename}!</p><p>You have been Invited to join <b>{inviteorganizationname} </b></p><p>You can start exploring the resources or showcasing your work.</p><p>Kindly click below button to Join</p>
    <a style='background-color:#7367f0;padding:12px;color:#fff'target='_blank' href={invitelink} >Join</a>`
    const test = email_content.replace('{invitename}', invite.name).replace('{inviteorganizationname}', invite.organizationname)
    setEmailContent(test)
  }

  useEffect(() => {

    if (Object.keys(invite).length > 0) {
      getThankyouNotes()

      setToEmail(invite.email)
    }
  }, [invite])

  const sendMail = () => {
    axios.post('/invitations/invitationmail', { content: emailContent, id: invite.id })
      .then(() => {
        toggleSidebar(false)
      })
    console.log(() => { })
  }

  return (
    <Sidebar
      size='lg'
      open={open}
      title='Send Invitation'
      headerClassName='mb-1'
      contentClassName='p-0'
      bodyClassName='pb-sm-0 pb-3'
      toggleSidebar={toggleSidebar}
    >
      <Form>
        <div className='mb-1'>
          <h4 for='invite-message' className='form-label mt-1'>To</h4>
          <Badge color='light-primary'>{toEmail}</Badge>
        </div>
        <div className='mb-1'>
          <h4 for='invite-message' className='form-label mt-1'>
            Message
          </h4>
          <div contentEditable={true} dangerouslySetInnerHTML={{ __html: emailContent }}>
          </div>
        </div>
        <div className='d-flex flex-wrap mt-2'>
          <Button className='me-1' color='primary' onClick={() => { sendMail() }} >
            Send
          </Button>
          <Button color='secondary' outline onClick={toggleSidebar}>
            Cancel
          </Button>
        </div>
      </Form>
    </Sidebar>
  )
}

export default SidebarSendInvoice
