// ** React Imports
import { Fragment, useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import Avatar from '@components/avatar'
import { Can } from '@src/utility/context/Can.js'
// ** Store & Actions
import { invitationsList, userList, deleteUser, deleteInvitation, updateStatus } from '../store'
import { useDispatch, useSelector } from 'react-redux'
import Select from 'react-select'
import SidebarSendMail from '../shared-sidebar/SidebarSendMail'
// ** Third Party Components
import ReactPaginate from 'react-paginate'
import { MoreVertical, Trash2, Eye, Edit, Send, CheckCircle, XCircle } from 'react-feather'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
// ** Utils
import { selectThemeColors } from '@utils'
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'
// ** Reactstrap Imports
import {
  Row,
  Col,
  Card,
  Input,
  Label,
  Button,
  CardBody,
  DropdownMenu,
  DropdownItem,
  DropdownToggle,
  Badge,
  UncontrolledDropdown,
  Table
} from 'reactstrap'
const MySwal = withReactContent(Swal)
// ** Styles
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
import { orgUserId } from '@src/helper/sassHelper'
const userId = orgUserId()
// ** Table Header
const CustomHeader = ({ handlePerPage, rowsPerPage, handleFilter, searchTerm }) => {

  return (
    <div className='invoice-list-table-header w-100 me-1 ms-50 mt-2 mb-75'>
      <Row>
        <Col xl='6' md='6' sm='6' className='d-flex align-items-center p-0'>
          <div className='d-flex align-items-center w-100'>
            <label htmlFor='rows-per-page'>Show</label>
            <Input
              className='mx-50'
              type='select'
              id='rows-per-page'
              value={rowsPerPage}
              onChange={handlePerPage}
              style={{ width: '5rem' }}
            >
              <option value='10'>10</option>
              <option value='25'>25</option>
              <option value='50'>50</option>
            </Input>
            <label htmlFor='rows-per-page'>Entries</label>
          </div>
        </Col>
        <Col md='6' sm='6'
          xl='6'
          className='d-flex align-items-sm-center justify-content-xl-end justify-content-start flex-xl-nowrap flex-wrap flex-sm-row flex-column pe-xl-1 p-0 mt-xl-0 mt-1'
        >
          <div className='d-flex align-items-center mb-sm-0 mb-1 me-1'>
            <label className='mb-0' htmlFor='search-invoice'>
              Search:
            </label>
            <Input
              id='search-invoice'
              className='ms-50 w-100'
              type='text'
              value={searchTerm}
              onChange={e => handleFilter(e.target.value)}
            />
          </div>

          <div className='d-flex align-items-center table-header-actions'>
            <Can I={'write'} a={'team'}>
              <Button color='primary' tag={Link} to='/team/add'>
                Add Employee
              </Button>
            </Can>
          </div>
        </Col>
      </Row>
    </div>
  )
}

const UsersList = () => {
  // ** Store Vars
  const dispatch = useDispatch()
  const store = useSelector(state => state.team)

  const statusOptions = [
    { value: '', label: 'Select All' },
    { value: '1', label: 'Active' },
    { value: '2', label: 'Inactive' },
    { value: '3', label: 'Invited' }
  ]
  // ** States
  const [searchTerm, setSearchTerm] = useState('')
  const [currentPage, setCurrentPage] = useState(1)
  const [rowsPerPage, setRowsPerPage] = useState(10)
  const [currentStatus, setCurrentStatus] = useState({ value: '1', label: 'Active', number: 0 })
  const [selectedData, setSelectedData] = useState({})
  const [sendSidebarOpen, setSendSidebarOpen] = useState(false)
  const toggleSendSidebar = () => setSendSidebarOpen(!sendSidebarOpen)

  const renderClient = row => {

    return (
      <Avatar
        initials
        className='me-1'
        color={'light-primary'}
        content={row.name || 'John Doe'}
      />
    )
  }
  const [block, setBlock] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }
  const deletefn = (row) => {

    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ms-1'
      },
      buttonsStyling: false
    }).then(async (result) => {
      if (result.value) {
        const opt = row.usertype === '1' ? 'User' : 'invitation'
        if (row.usertype === '1') {
          await dispatch(deleteUser(row.id))
        } else {
          await dispatch(deleteInvitation(row.id))
        }
        MySwal.fire({
          icon: 'success',
          title: 'Deleted!',
          text: `${opt} has been deleted.`,
          customClass: {
            confirmButton: 'btn btn-success'
          }
        })
      }
    })
  }

  const updateStatusfn = (id, sts) => {

    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: `Yes, ${sts === 1 ? 'Mark as Active' : 'Mark as Inactive'} it!`,
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ms-1'
      },
      buttonsStyling: false
    }).then(async (result) => {
      if (result.value) {
        const obj = {
          status: sts,
          updatedBy: userId,
          id
        }
        await dispatch(updateStatus(obj))

        MySwal.fire({
          icon: 'success',
          title: `${sts === 1 ? 'Mark as Active' : 'Mark as Inactive'}!`,
          text: `User has been Marked as ${sts === 1 ? 'Active' : 'Inactive'}.`,
          customClass: {
            confirmButton: 'btn btn-success'
          }
        })
      }
    })
  }

  const inviteStatusObj = [
    '',
    'light-primary'
  ]

  const userStatusObj = [
    '',
    'light-success',
    'light-danger'
  ]

  const statusArr = [
    '',
    'Invited',
    'Inactive'
  ]

  const userstatusArr = [
    '',
    'Active',
    'Inactive'
  ]

  // ** Get data on mount
  useEffect(async () => {
    setBlock(true)
    if (currentStatus.value === '3') {
      await dispatch(
        invitationsList({
          q: searchTerm,
          page: currentPage,
          perPage: rowsPerPage,
          status: currentStatus.value
        })
      )
    } else {
      await dispatch(
        userList({
          q: searchTerm,
          page: currentPage,
          perPage: rowsPerPage,
          status: currentStatus.value
        })
      )
    }
    setBlock(false)
  }, [dispatch])

  // ** Function in get data on page change
  const handlePagination = async page => {
    setBlock(true)
    if (currentStatus.value === '3') {
      await dispatch(
        invitationsList({
          q: searchTerm,
          perPage: rowsPerPage,
          page: page.selected + 1,
          status: currentStatus.value
        })
      )
    } else {
      await dispatch(
        userList({
          q: searchTerm,
          perPage: rowsPerPage,
          page: page.selected + 1,
          status: currentStatus.value
        })
      )
    }
    setBlock(false)
    setCurrentPage(page.selected + 1)
  }

  // ** Function in get data on rows per page
  const handlePerPage = async e => {
    const value = parseInt(e.currentTarget.value)
    setBlock(true)
    if (currentStatus.value === '3') {
      await dispatch(
        invitationsList({
          q: searchTerm,
          perPage: value,
          page: currentPage,
          status: currentStatus.value
        })
      )
    } else {
      await dispatch(
        userList({
          q: searchTerm,
          perPage: value,
          page: currentPage,
          status: currentStatus.value
        })
      )
    }
    setBlock(false)
    setRowsPerPage(value)
  }

  // ** Function in get data on search query change
  const handleFilter = async val => {
    setSearchTerm(val)
    setBlock(true)
    if (currentStatus.value === '3') {
      await dispatch(
        invitationsList({
          q: val,
          page: currentPage,
          perPage: rowsPerPage,
          status: currentStatus.value
        })
      )
    } else {
      await dispatch(
        userList({
          q: val,
          page: currentPage,
          perPage: rowsPerPage,
          status: currentStatus.value
        })
      )
    }
    setBlock(false)
  }

  // ** Custom Pagination
  const CustomPagination = () => {
    const count = Number(Math.ceil(store.total / rowsPerPage))

    return (
      <ReactPaginate
        previousLabel={''}
        nextLabel={''}
        pageCount={count || 1}
        activeClassName='active'
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={page => handlePagination(page)}
        pageClassName={'page-item'}
        nextLinkClassName={'page-link'}
        nextClassName={'page-item next'}
        previousClassName={'page-item prev'}
        previousLinkClassName={'page-link'}
        pageLinkClassName={'page-link'}
        containerClassName={'pagination react-paginate justify-content-end my-2 pe-1'}
      />
    )
  }

  // ** Table data to render
  const dataToRender = () => {
    const filters = {
      status: currentStatus.value,
      q: searchTerm
    }

    const isFiltered = Object.keys(filters).some(function (k) {
      return filters[k].length > 0
    })

    if (store.data.length > 0) {
      return store.data
    } else if (store.data.length === 0 && isFiltered) {
      return []
    } else {
      return []
    }
  }

  return (
    <Fragment>
      <Card>
        <CardBody>
          <Row>
            <Col md='3'>
              <Label for='status-select'>Status</Label>
              <Select
                theme={selectThemeColors}
                isClearable={false}
                className='react-select'
                classNamePrefix='select'
                options={statusOptions}
                value={currentStatus}
                onChange={async data => {
                  setCurrentStatus(data)
                  setBlock(true)
                  if (data.value === '3') {
                    await dispatch(
                      invitationsList({
                        q: searchTerm,
                        page: currentPage,
                        status: data.value,
                        perPage: rowsPerPage
                      })
                    )
                  } else {
                    await dispatch(
                      userList({
                        q: searchTerm,
                        page: currentPage,
                        status: data.value,
                        perPage: rowsPerPage
                      })
                    )
                  }
                  setBlock(false)
                }}
              />
            </Col>
          </Row>
        </CardBody>
      </Card>
      <UILoader blocking={block} loader={<Loader />}>
        <Card className='overflow-hidden'>
          <div className='react-dataTable px-2'>
            <CustomHeader
              store={store}
              searchTerm={searchTerm}
              rowsPerPage={rowsPerPage}
              handleFilter={handleFilter}
              handlePerPage={handlePerPage}
            />
            <Table className='table-hover'>
              <thead>
                <tr>
                  <th>User</th>
                  <th>Role</th>
                  <th>Designation</th>
                  <th>Department</th>
                  <th>Status</th>
                  <th>Action</th>
                </tr>
              </thead>

              <tbody>
                {
                  dataToRender().map((row, k) => {
                    return (
                      <tr key={k}>
                        <td>
                          <div className='d-flex justify-content-left align-items-center'>
                            {renderClient(row)}
                            <div className='d-flex flex-column'>
                              <Link
                                to={`/team/view/${row.id}`}
                                className='user_name text-truncate text-body'
                              >
                                <span className='fw-bolder'>{row.name}</span>
                              </Link>
                              <small className='text-truncate text-muted mb-0'>{row.email}</small>
                            </div>
                          </div>
                        </td>
                        <td>
                          <span className='text-capitalize'>{row.rolename}</span>
                        </td>
                        <td> <span className='text-capitalize'>{row.designationname}</span></td>
                        <td> <span className='text-capitalize'>{row.departmentname}</span></td>
                        <td>
                          <Badge className='text-capitalize' color={row.usertype === '1' ? userStatusObj[row.status] : inviteStatusObj[row.status]} pill>
                            {row.usertype === '1' ? userstatusArr[row.status] : statusArr[row.status]}
                          </Badge>
                        </td>
                        <td>
                          <div className='column-action d-flex align-items-center'>
                            <Can I={'read'} a={'team'}>
                              <Col to={`/team/view/${row.id}`}>
                                <Eye
                                  className='cursor-pointer mt-0' size={16} />
                              </Col>
                            </Can>
                            {row.usertype !== '1' &&
                              <Can I={'read'} a={'team'}>
                                <Col
                                  onClick={() => { setSelectedData(row); toggleSendSidebar() }} >
                                  <Send
                                    className='cursor-pointer ms-1 mt-0' size={16} />
                                </Col>
                              </Can>
                            }
                            <UncontrolledDropdown>
                              <DropdownToggle tag='div' className='btn btn-sm'>
                                <MoreVertical size={14} className='cursor-pointer' />
                              </DropdownToggle>
                              <DropdownMenu>
                                <Can I={'delete'} a={'team'}>
                                  <DropdownItem
                                    tag='a'
                                    href='/'
                                    className='w-100'
                                    onClick={e => {
                                      e.preventDefault()
                                      deletefn(row)
                                    }}
                                  >
                                    <Trash2 size={14} className='me-50' />
                                    <span className='align-middle'>Delete</span>
                                  </DropdownItem>
                                </Can>
                                {(row.usertype === '1' && row.status === 2) &&
                                  <Can I={'delete'} a={'team'}>
                                    <DropdownItem
                                      tag='a'
                                      href='/'
                                      className='w-100'
                                      onClick={e => {
                                        e.preventDefault()
                                        updateStatusfn(row.id, 1)
                                      }}
                                    >
                                      <CheckCircle size={14} className='me-50' />
                                      <span className='align-middle'>Mark as Active</span>
                                    </DropdownItem>
                                  </Can>
                                }
                                {(row.usertype === '1' && row.status === 1) &&
                                  <Can I={'write'} a={'team'}>
                                    <DropdownItem
                                      tag='a'
                                      href='/'
                                      className='w-100'
                                      onClick={e => {
                                        e.preventDefault()
                                        updateStatusfn(row.id, 2)
                                      }}
                                    >
                                      <XCircle size={14} className='me-50' />
                                      <span className='align-middle'>Mark as Inactive</span>
                                    </DropdownItem>
                                  </Can>
                                }
                                <Can I={'write'} a={'team'}>
                                  <DropdownItem
                                    tag={Link} lg={4}
                                    to={`/team/${row.usertype === '1' ? 'edit' : 'edit-invitation'}/${row.id}`}
                                    className='w-100'
                                  >
                                    <Edit size={14} className='me-50' />
                                    <span className='align-middle'>Edit</span>
                                  </DropdownItem>
                                </Can>
                              </DropdownMenu>
                            </UncontrolledDropdown>
                          </div>
                        </td>
                      </tr>

                    )
                  })
                }
              </tbody>
            </Table>
            <CustomPagination />
          </div>

          <SidebarSendMail toggleSidebar={toggleSendSidebar} open={sendSidebarOpen} invite={selectedData} />
        </Card>
      </UILoader>
    </Fragment>
  )
}

export default UsersList
